import React, { useState } from 'react';
import './Ref.scss';
import friendsText from './assets/letter.png'; //     "friends"

const Ref: React.FC = () => {
  const handleShare = () => {
    if (navigator.share) {
      navigator
        .share({
          title: '  ',
          text: '   !',
          url: 'https://www.youtube.com/watch?v=dQw4w9WgXcQ', //    
        })
        .then(() => {
          console.log(' ');
        })
        .catch((error) => {
          console.error('  ', error);
          alert('   .  .');
        });
    } else {
      alert('Web Share API     .');
    }
  };

  return (
    <div className="ref-container">
      <div className="ref-button" onClick={handleShare}>
        <img src={friendsText} alt="Friends Text" className="friends-text" />
      </div>
    </div>
  );
};

export default Ref;
